.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 30px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: rgb(252, 252, 252);
    z-index: 1;
}

.star-space {
    display: flex;
    margin: 10px;
}

.product_rating {
    display: flex;
    justify-content: center;
    margin: 20px;
}

.product>img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin: 20px;
}

.product>button {
    background: rgb(0, 114, 188);
    padding: 10px;
    border-radius: 5px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #fff;
    font-weight: bold;
}

.product_price {
    display: flex;
    justify-content: center;
}

.product_info {
    height: 100px;
    margin-bottom: 15px;
    justify-content: center;
}